.page-header {
  height: 40px;
  background-color: #000;
  position: relative;
  z-index: 100000;
}
.page-header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}


.page-logo {
  margin:0;
  display: flex;
  align-items: center;
}
.page-logo-link {
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  text-decoration: none;
}
.page-logo-link span {
  color: var(--color);
}



.page-nav {}
.page-nav-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.page-nav-list li {
  position: relative;
}
.page-nav-list a {
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
  padding: 0 20px;
  line-height: 40px;
  display: block;
  transition: 0.5s;
}
.page-nav-list a:hover {
  color: var(--color);
}


.page-nav-sublist {
  position: absolute;
  top: calc(100% + 12px);
  left: 50%;
  background: #000;
  list-style:none;
  padding:10px;
  margin:0;
  transform: translate(-50%, 0);
  /* do plynnego chowania i pokazywania elementu */
  transition:0.5s;
  opacity: 0;
  visibility: hidden;
}

.page-nav-list li:hover .page-nav-sublist {
  opacity: 1;
  visibility: visible;
}

.page-nav-sublist:after {
  content:"";
  width:0;
  height:0;
  border:10px solid transparent;
  border-bottom-color: #000;
  display: block;
  position: absolute;
  top:-20px;
  left: 50%;
  transform: translate(-50%, 0);
}
.page-nav-sublist:before {
  content: "";
  width: 100%;
  height: 12px;
  background: transparent;
  position: absolute;
  left:0;
  top:-12px;
}
.page-nav-sublist li {

}
.page-nav-sublist a {
  text-transform: none;
  font-weight: normal;
  line-height: 25px;
}